export default {
	data(){
		return{
			isHide:true,
			indexData:{}
		}
	},
	onLoad(){
		uni.request({
			url:"http://localhost:3001/getIndexData",
			// dataType:"json",
			success:(data)=>{
				this.indexData = data.data;
				console.log(data);
				},
			fail:()=>{console.log("请求失败")}
		})
	},
	methods:{
		point(isHide){
			this.isHide = !isHide
		},
		goSearch(){
			uni.navigateTo({
				url:"../Search/index"
			})
		},
		goPersonal(){
			uni.switchTab({
				url:"../Personal/index"
			})
		}
	}
}
